<template>
  
    <div class="weather-container">
    <h2 class="title">Погода</h2>
    <hr />
    <div class="city-selection">
      <label for="city">Выберите город:</label>
      <select id="city" v-model="selectedCity" @change="fetchWeatherData">
        <option v-for="city in cities" :key="city.id" :value="city.name">{{ city.name }}</option>
      </select>
      <button type="button" class="btn-fetch" @click="fetchWeatherData">Узнать погоду</button>
    </div>
    <hr />
    <div class="content" v-if="fetchedItems && fetchedItems.main && fetchedItems.weather && fetchedItems.wind">
      <h3>Погодные условия для {{ selectedCity }}</h3>
      <table class="weather-table">
        <tbody>
          <tr>
            <td class="temperature">Температура: {{ fetchedItems.main.temp }} °C</td>
            <td>Влажность: {{ fetchedItems.main.humidity }}%</td>
            <td>Давление: {{ fetchedItems.main.pressure }} ГПа</td>
            <td>Погода: {{ fetchedItems.weather[0].description }}</td>
            <td>
              <img :src="`http://openweathermap.org/img/wn/${fetchedItems.weather[0].icon}@2x.png`" alt="Weather Icon" class="weather-icon" />
            </td>
            <td>Скорость ветра: {{ fetchedItems.wind.speed }} М/с <br /> Направление ветра: {{ fetchedItems.wind.deg }} °</td>
          </tr>
        </tbody>
      </table>
      <WeatherForecast :forecastData="forecastData" />
    </div>
  </div>

</template>

<script>

import axios from 'axios';
import WeatherForecast from './components/WeatherForecast.vue'; // Импортируем новый компонент

export default {
  components: {
    WeatherForecast, // Регистрация компонента
  },
  data() {
    return {
      selectedCity: 'Москва',
      cities: [{ id: 1, name: 'Москва' },
        { id: 2, name: 'Санкт-Петербург' },
        { id: 3, name: 'Новосибирск' },
        { id: 4, name: 'Екатеринбург' },
        { id: 5, name: 'Казань' },], // Заполните список городов по вашему усмотрению
      fetchedItems: null,
      forecastData: null,
    };
  },
  methods: {
    async fetchWeatherData() {
      const weatherResponse = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${this.selectedCity}&appid=cffba3f24431c96f75229c34b22fba74&units=metric`);
      this.fetchedItems = weatherResponse.data;

      const forecastResponse = await axios.get(`https://api.openweathermap.org/data/2.5/forecast?q=${this.selectedCity}&appid=cffba3f24431c96f75229c34b22fba74&units=metric`);
      this.forecastData = forecastResponse.data.list; // Данные прогноза
    },
  },
};

</script>

