<template>
    <div class="forecast-container">
      <h4>Прогноз погоды на следующие дни</h4>
      <div class="forecast-grid">
        <div class="forecast-card" v-for="(forecast, index) in forecastData" :key="index">
          <h5>{{ formatDate(forecast.dt_txt) }}</h5>
          <p>Температура: {{ forecast.main.temp }} °C</p>
          <p>Влажность: {{ forecast.main.humidity }}%</p>
          <p>Давление: {{ forecast.main.pressure }} ГПа</p>
          <p>Погода: {{ forecast.weather[0].description }}</p>
          <p><img :src="`http://openweathermap.org/img/wn/${forecast.weather[0].icon}@2x.png`" alt="Weather Icon" /></p>
          <p>Скорость ветра: {{ forecast.wind.speed }} М/с <br /> Направление ветра: {{ forecast.wind.deg }} °</p>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      forecastData: {
        type: Array,
        required: true,
      },
    },
    methods: {
      formatDate(dateString) {
        const date = new Date(dateString);
        return date.toLocaleDateString();
      },
    },
  };
  </script>
  
  <style scoped>
  .forecast-container {
    margin-top: 20px;
  }
  
  .forecast-grid {
    display: flex;
    flex-wrap: wrap;
  }
  
  .forecast-card {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 10px;
    margin: 5px;
    flex: 1 1 180px; /* Адаптивная ширина карточек */
    text-align: center;
  }
  
  </style>